.education-section {
  padding: 50px 20px;
  background-color: #f9f9f9;
}

.section-title {
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  color: #333;
  margin-bottom: 10px;
}

.section-divider {
  width: 80px;
  height: 4px;
  background-color: #138781;
  border: none;
  margin: 0 auto 30px auto;
}

.timeline {
  position: relative;
  margin: 0 auto;
  padding: 20px 0;
  width: 80%;
}

.timeline::before {
  content: "";
  position: absolute;
  left: 20px;
  top: 0;
  bottom: 0;
  width: 4px;
  background: #138781;
}

.timeline-item {
  position: relative;
  margin-bottom: 40px;
  padding-left: 60px;
}

.timeline-icon {
  position: absolute;
  left: 0;
  top: 0;
  background: #138781;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.timeline-content {
  background: white;
  padding: 15px 20px;
  border-radius: 6px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
}

.timeline-date {
  font-size: 0.9rem;
  color: #888;
}

.timeline-degree {
  font-size: 1.2rem;
  font-weight: bold;
  margin: 5px 0;
}

.timeline-school {
  font-size: 1rem;
  color: #555;
}

#dark .education-section {
  background-color: rgba(255, 255, 255, 0.095);
}
/* ========================== */
@media (max-width: 600px) {
 .education-section {
    margin: 0;
    padding: 0;
    margin-top: 30px;
  }
 .education-section h2 {
    padding-top: 20px;
  }
.education-section hr {
    width: 280px;
    margin-bottom: 10px;
  }
}